import React from "react";
import { Card, CardContent, CardMedia, Grid, Typography } from "@mui/material";
import photographerImage from "../images/profile.jpg";

const AboutMe = () => {
    return (
        <div style={{ display: "flex", justifyContent: "center", marginTop: "80px", marginBottom: "80px" }}>
            <Card sx={{ maxWidth: 1200 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                        <div style={{ display: "flex", margin: "20px" }}>
                            <CardMedia
                                component="img"
                                sx={{ width: "100%", height: "auto", objectFit: "contain", marginRight: "20px" }}
                                image={photographerImage}
                                alt="Photographer"
                            />
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <CardContent>
                            <Typography variant="body1" sx={{ fontSize: "1.2rem" }}>
                                &nbsp;&nbsp;&nbsp;Nac en la ciudad de Rosario.<br />
                                &nbsp;&nbsp;&nbsp;En 5to grado por un taller de fotografa al que me anot y que me obsequiaron una cmara Konica. En 2007 comenc a estudiar fotografa en la Escuela de Artes Plsticas Manuel Musto. All trabaj en el laboratorio aprendiendo fotografa analgica en blanco y negro con Andrea Ostera.
                                Tambin en esa institucin particip en talleres de pintura, serigrafa con Hugo Cava, historia del arte contemporneo con Nancy Rojas y heliocopia con Mabel Rodrguez.<br />
                                &nbsp;&nbsp;&nbsp;Particip en diversos talleres sobre produccin con Norberto Puzzolo, Gisela Vol de la subcooperativa de fotgrafos y Gustavo Fritegotto, este ltimo sobre el Archivo familiar.<br />
                                &nbsp;&nbsp;&nbsp;En el ao 2018 complet mis estudios terciarios en fotografa en ISET n18 con especializacin en medios digitales.<br />
                                &nbsp;&nbsp;&nbsp;En el ao 2013 particip en "Interferencias", un programa radial de experimentacin y arte sonoro, a partir de un taller con Leonello Zambn sobre Gambiarras sonoras.<br />
                                &nbsp;&nbsp;&nbsp;En 2016, se me otorg el segundo premio en fotografas en serie de la obra del Arq. Rafael Iglesia. La muestra se realiz del 13 de octubre al 20 de noviembre en las galeras del CCPE.<br />
                                &nbsp;&nbsp;&nbsp;Desde 2016 me he estado formando en clases particulares de canto con Liza Polichizzo, habiendo pasado por un breve lapso por la facultad de msica.<br />
                                &nbsp;&nbsp;&nbsp;Desde 2018 estoy incursionando en la filmacin de cortos y videoclips.<br />
                                &nbsp;&nbsp;&nbsp;Desde 2017 me encuentro explorando la tcnica de cianotipo.
                            </Typography>
                        </CardContent>
                    </Grid>
                </Grid>
            </Card>
        </div>
    );
}

export default AboutMe;
